<template>
    <Transition name="modal">
        <div class="modal" @click="closeModal">
        <div class="modal__block" @click.stop="">
            <h2 class="modal__block-title">
                {{ edit ? 'Изменить заметку' : 'Добавить заметку' }}
            </h2>
            <div class="modal__block-inputs">
                <label>
                    <span>Title</span>
                    <input type="text" v-model="name">
                </label>
                <label>
                    <span>Content</span>
                    <textarea v-model="text"></textarea>
                </label>
            </div>
            <div class="modal__block-btns">
                <button class="modal__block-btns-btn del" @click="closeModal">
                    Отмена
                </button>
                <button v-if="!edit" class="modal__block-btns-btn edit" @click="addNote">
                    Добавить
                </button>
                <button v-else class="modal__block-btns-btn edit" @click="editNote">
                    Изменить
                </button>
            </div>
        </div>
        </div>
    </Transition>
</template>

<script>
    export default {
        props: {
            edit: {
                typeof: Boolean 
            },
            editedNote: {
                typeof: Object
            }
        },
        data() {
            return {
                name: '',
                text: ''
            }
        },
        methods: {
            closeModal() {
                this.$emit('closeModal')
                this.name = this.text = ''  
            },
            editNote() {
                if(this.name.length > 2 && this.text.length > 2) {
                    const editNote = {
                        id: this.editedNote.id,
                        name: this.name,
                        text: this.text,
                        date: new Date().toLocaleDateString()
                    }
                    this.$emit('editNote', editNote)
                    this.closeModal()                                   
                }
            },
            addNote() {
                if(this.name != '' && this.text !='') {
                    const note = {
                    name: this.name,
                    text: this.text,
                    date: new Date().toLocaleDateString()
                }
                this.$emit('addNote', note)
                this.closeModal()
                }
            }
        },
    }
</script>

